.lm-button {
  display: inline-block;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: #fff;
  border: 1px solid #dcdfe6;
  color: #606266;
  -webkit-appearance: none;
  text-align: center;
  box-sizing: border-box;
  outline: none;
  margin: 0;
  transition: 0.1s;
  font-weight: 500;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  padding: 12px 20px;
  font-size: 14px;
  border-radius: 4px;
}
.lm-button [class*=lm-icon-] + span {
  margin-left: 5px;
}
.lm-button:hover, .lm-button:focus {
  color: #409eff;
  border-color: #c6e2ff;
  background-color: #ecf5ff;
}
.lm-button.is-plain:hover, .lm-button.is-plain:focus {
  background: #fff;
  border-color: #409eff;
  color: #409eff;
}
.lm-button--primary {
  color: #fff;
  background-color: #409eff;
  border-color: #409eff;
}
.lm-button--primary:hover, .lm-button--primary:focus {
  background: #66b1ff;
  border-color: #66b1ff;
  color: #fff;
}
.lm-button--primary.is-plain {
  color: #409eff;
  background: #ecf5ff;
  border-color: #b3d8ff;
}
.lm-button--primary.is-plain:hover, .lm-button--primary.is-plain:focus {
  background: #409eff;
  border-color: #409eff;
  color: #fff;
}
.lm-button--primary.is-disabled {
  color: #aaa;
  background: #eee;
  border-color: #eee;
}
.lm-button--success {
  color: #fff;
  background-color: #67c23a;
  border-color: #67c23a;
}
.lm-button--success:hover, .lm-button--success:focus {
  background: #85ce61;
  border-color: #85ce61;
  color: #fff;
}
.lm-button--success.is-plain {
  color: #67c23a;
  background: #f0f9eb;
  border-color: #c2e7b0;
}
.lm-button--success.is-plain:hover, .lm-button--success.is-plain:focus {
  background: #67c23a;
  border-color: #67c23a;
  color: #fff;
}
.lm-button--success.is-disabled {
  color: #aaa;
  background: #eee;
  border-color: #eee;
}
.lm-button--info {
  color: #fff;
  background-color: #909399;
  border-color: #909399;
}
.lm-button--info:hover, .lm-button--info:focus {
  background: #a6a9ad;
  border-color: #a6a9ad;
  color: #fff;
}
.lm-button--info.is-plain {
  color: #909399;
  background: #f4f4f5;
  border-color: #d3d4d6;
}
.lm-button--info.is-plain:hover, .lm-button--info.is-plain:focus {
  background: #909399;
  border-color: #909399;
  color: #fff;
}
.lm-button--info.is-disabled {
  color: #aaa;
  background: #eee;
  border-color: #eee;
}
.lm-button--warning {
  color: #fff;
  background-color: #e6a23c;
  border-color: #e6a23c;
}
.lm-button--warning:hover, .lm-button--warning:focus {
  background: #ebb563;
  border-color: #ebb563;
  color: #fff;
}
.lm-button--warning.is-plain {
  color: #e6a23c;
  background: #fdf6ec;
  border-color: #f5dab1;
}
.lm-button--warning.is-plain:hover, .lm-button--warning.is-plain:focus {
  background: #e6a23c;
  border-color: #e6a23c;
  color: #fff;
}
.lm-button--warning.is-disabled {
  color: #aaa;
  background: #eee;
  border-color: #eee;
}
.lm-button--danger {
  color: #fff;
  background-color: #f56c6c;
  border-color: #f56c6c;
}
.lm-button--danger:hover, .lm-button--danger:focus {
  background: #f78989;
  border-color: #f78989;
  color: #fff;
}
.lm-button--danger.is-plain {
  color: #f56c6c;
  background: #fef0f0;
  border-color: #fbc4c4;
}
.lm-button--danger.is-plain:hover, .lm-button--danger.is-plain:focus {
  background: #f56c6c;
  border-color: #f56c6c;
  color: #fff;
}
.lm-button--danger.is-disabled {
  color: #aaa;
  background: #eee;
  border-color: #eee;
}
.lm-button.is-round {
  border-radius: 20px;
  padding: 12px 23px;
}
.lm-button.is-circle {
  border-radius: 50%;
  padding: 12px;
}